.card{
    display: inline-block;
    width: 230px;
    height: 200px;
    background-color:blue;
    list-style-type: none;
    margin: 10px ;
    text-align: center;
    border-radius:5px;
    transition: transform .2s;
}

.card:hover{
    transform:scale(1.1);
}

.card .card__main .card__inner p ,span{
    margin: 5px;
    
}

.card .card__main .card__inner .card__name span{
    font-size:15px
}

.card .card__main .card__inner .card__name p{
    font-size:25px;
    font-weight:500;
    
}
.card .card__main .card__inner .card__total{
    font-size:35px;
    font-weight:500;
    text-shadow:2px 2px 2px pink;
}


